var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',[_c('project-header',{attrs:{"route":"/codesnippets"}}),_c('div',{staticClass:"responsive"},[_c('h4',[_vm._v("TL;DR")]),_c('p',[_vm._v("I designed an interface where users can input multiple \"code snippets\" to transform their data.")]),_c('h4',[_vm._v("Problem")]),_c('styled-image',{attrs:{"src":require("../../assets/codesnippets/3vs1.png"),"alt":"Combine three nodes into one"}}),_c('p',[_vm._v("A project I worked on for KNIME was to create an interface for a node (KNIME term for encapsulated functionality) where users can create small code snippets to manipulate data. For example, users might want to create a column with the mean of three other columns or cut the surname of a \"name surname\" formatted column. Previously, this was possible with a String Manipulation node for Strings, a Math Formula node for numerical data and a Rule Engine node for conditions. But what if you needed to alter Strings based on conditions? Then it gets complicated. The new node should make it easier for users to create more complex logic.")]),_c('h4',[_vm._v("Research")]),_c('styled-image',{attrs:{"src":require("../../assets/codesnippets/code-versus.png"),"alt":"Use of space in the String Manipulation interface"}}),_c('p',[_vm._v("I analyzed the current UI and interaction of the already existing nodes and found out that they had several disadvantages:")]),_vm._m(0),_c('h4',[_vm._v("Concept")]),_c('styled-image',{attrs:{"src":require("../../assets/codesnippets/variations-codesnippets.png"),"alt":"Two concepts for the code editor: Columns, variables and functions in dropdowns (left) or in a tabbed interface (right)"}}),_c('p',[_vm._v("I improved on these with a design that put the code textfield with a large amount of space in the center of attention. Adding columns, variables and functions was now one click away but this saved precious screen real estate. I also added a small \"evaluate\" functionality that would check your code on the first row of your data so you could iteratively improve your code snippet without having to run it on your entire data each time. On the top an overview of all the code snippets was presented and you could add more code snippets or alter existing ones. The UI for the code editor was designed to make it reusable in other nodes (e.g. filtering rows according to a code snippet with a boolean statement).")]),_c('h4',[_vm._v("Evaluation")]),_c('styled-image',{attrs:{"src":require("../../assets/codesnippets/concept.png"),"alt":"Final UI concept"}}),_vm._m(1),_c('p',[_vm._v("In the design, however, there is - as always - still room for improvement. The emancipated display of the upper \"overview\" and the lower \"code editor\" component does not make it entirely clear that a selection of a code snippet in the \"overview\" component would change the display in the \"code editor\" component. This drawback was however favored over a dialog-in-a-dialog design.")]),_c('p',[_vm._v("Also, the design could guide new users better on how to start. They are presented with a blank textfield and a variety of UI components (much like opening a terminal for the first time). The functions which are the core of writing your snippet are hidden behind a dropdown. The aim of the design, however, was to create a good experience for experienced users who want to optimize their data manipulation workflows.")]),_c('p',[_vm._v("On the whole, I am very content with the process and outcome of this project. I was involved from the research phase over the conceptual phase to the implementation and even helped with programming parts of the \"code editor\" component. Also, the feedback from the community shows that the hours you invest in research and ideation really pay off in the end.")]),_c('styled-image',{attrs:{"src":require("../../assets/codesnippets/column_expressions.png"),"alt":"User interface as of KNIME Analytics Platform v3.7.1"}})],1),_c('project-arrows',{attrs:{"route":"/codesnippets"}})],1)}
